* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  width: 100%;
  padding: 40px;
  font-family: verdana;
  overflow-x: hidden;
  overflow-y: auto;
}

.ss-arrow {
  position: absolute;
  top: 28%;
  right: 6px;
}

.ss-content .ss-list {
  height: 300px;
  flex: auto;
  overflow-x: hidden;
  overflow-y: scroll;
}

img {
  object-fit: cover;
  display: block;
}

.breed-select {
  width: 100%;
  max-width: 320px;
  margin-bottom: 20px;
}

.error {
  color: #d12828;
  font-size: 16px;
  font-weight: bold;
  display: none;
}

.loader {
  width: 60px;
  height: 60px;
  box-sizing: border-box;
  border: 5px solid #d4d4d4;
  border-bottom-color: #69798a;
  border-radius: 50%;
  margin-top: -30px;
  margin-left: -30px;
  font-size: 0;
  animation: rotation .7s linear infinite;
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.cat-info {
  width: 100%;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: flex-start;
  margin: 0 auto;
  display: flex;
}

.cat-info img {
  width: 40%;
  max-height: 80vh;
}

.cat-info__text {
  width: calc(60% - 40px);
  flex-wrap: wrap;
  gap: 30px;
  display: flex;
}

.cat-info__text p {
  font-size: 18px;
  line-height: 1.35;
}

.cat-info__text p span {
  opacity: .6;
}

@media screen and (max-width: 767px) {
  body {
    padding: 24px;
  }

  .cat-info img {
    width: 100%;
  }

  .cat-info__text {
    width: 100%;
    gap: 20px;
    margin-top: 30px;
  }
}

